import { http } from '@/http';
import Toast from "vant/es/toast";
import "vant/es/toast/style/less";
import { computed, defineComponent, onMounted, ref } from 'vue';
import QRCode from 'qrcode';
import { useStore } from 'vuex';
export default defineComponent({
    name: 'BindFansQrcode',
    setup() {
        const qrcode = ref('');
        const store = useStore();
        const shopCode = computed(() => store.state.shopCode);
        const useqrcode = (url) => {
            const opts = {
                errorCorrectionLevel: 'H',
                type: 'image/jpeg',
                rendererOpts: { quality: 1 },
                margin: 3,
                width: 258
            };
            const canvas = document.getElementById('canvas');
            QRCode.toDataURL(canvas, url, opts, (err, url) => {
                if (err)
                    throw err;
                const img = document.getElementById('image');
                img.src = url;
            });
        };
        const getQrcode = () => {
            if (shopCode.value) {
                useqrcode(`http://${sessionStorage.getItem('prefix')}/orgmenu/auth?menuCode=sellerFansBind&shopCode=${shopCode.value}&bindChannel=3`);
            }
            else {
                http
                    .post('/hbSeller/sellerFans/shopBindCode', {}, false)
                    .then((res) => {
                    if (res.code === '200') {
                        const shopCode = res.data.shopCode;
                        store.commit('setShopCode', shopCode);
                        sessionStorage.setItem('shopCode', shopCode);
                        useqrcode(`http://${sessionStorage.getItem('prefix')}/orgmenu/auth?menuCode=sellerFansBind&shopCode=${shopCode}&bindChannel=3`);
                    }
                    else {
                        Toast.fail(res.msg);
                    }
                })
                    .catch((err) => {
                    console.log(err);
                });
            }
        };
        onMounted(() => {
            getQrcode();
        });
        return {
            qrcode
        };
    }
});
